import * as React from 'react';
import {
    Grid,
    // InputLabel, 
    // Typography,
    Link,
    Button
} from '@material-ui/core';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus, faDownload } from '@fortawesome/free-solid-svg-icons';
import { ReactTable } from 'Components/UI/TableComponent/ReactTable';
import { ReactSearch } from 'Components/UI/SearchBar/ReactSearch';
import { getRoles } from 'Pages/API/Calls/Roles';
import { Redirect } from 'react-router-dom';
import { Loader } from 'Pages/Lookup/Components/Loader';
import { ModalComponent } from 'Components/UI/ModalComponent/ModalComponent';
import BasicSelect from 'Components/UI/BasicSelect'

interface IProps {
    classes: any;
}

interface IState {
    rows: any;
    columns: any;
    loading: boolean;
    redirect_to_role: boolean;
    redirect_to_addrole: boolean;
    redirect_data: any;
    isModuleModalOpen: boolean;
    moduleItems: any[];
    moduleValue: number;
    totalCount: number;
    currentPage: number;
    limit: number;
    order: string;
    typingTimeout: any;
    default_filter: any;
    query: string;
    skip: number;
}

export class RolesTableList extends React.Component<IProps, IState> {
    constructor(props) {
        super(props);
        const moduleItems = [
            {
                name: 'User Management',
                value: 0
            },
            {
                name: 'Membership',
                value: 1
            },
            {
                name: 'Underwriting',
                value: 2
            },
            {
                name: 'Customer Care',
                value: 3
            },
            {
                name: 'Claims',
                value: 4,
            },
            // {
            //     name: 'Billing',
            //     value: 5,
            // },
        ]
        const columns = [
            {
                name: 'name',
                title: 'Role Name',
            },
            {
                name: 'role_id',
                title: 'Role ID',
            },
            {
                name: 'associate_groups',
                title: 'Associated Groups',
            },
            {
                name: 'module',
                title: 'Module',
            },
            {
                name: 'status',
                title: 'Status',
            },
        ];

        this.state = {
            loading: true,
            columns: columns,
            rows: [],
            redirect_to_role: false,
            redirect_to_addrole: false,
            redirect_data: [],
            isModuleModalOpen: false,
            moduleItems: moduleItems,
            moduleValue: 0,
            totalCount: 0,
            currentPage: 0,
            typingTimeout: 0,
            limit: 10,
            order: '',
            default_filter: [],
            query: '',
            skip: 0
        }
    }



    componentDidMount() {
        let _self = this;
        this.setState({
            loading: true,
        }, () => {
            _self.APIGetRolesList(this.state.currentPage, this.state.limit, this.state.query);
        });
    }

    APIGetRolesList = (page: number, limit: number, query: string) => {
        this.setState({
            loading: true,
        })

        let skip = page * limit

        this.setState({
            currentPage: page
        }, () => {
            this.setState({
                skip: skip
            })
        })


        let filter = {
            limit: limit,
            skip: skip
        }
        
        getRoles(filter, this.state.default_filter, this.state.order, query).then(res => {
            console.log((Math.ceil(res.count / limit) - 1))
            this.setState({
                rows: res.body,
                loading: false,
                totalCount: res.count,
                currentPage: this.state.currentPage > (Math.ceil(res.count / limit) - 1) ? (Math.ceil(res.count / limit) - 1) : page,
                limit: limit
            });
        }).catch(() => {
            this.setState({
                rows: [],
                loading: false,
                totalCount: 0,
            });
        })
    }


    handleTimeOutSearchTableRoles = (data) => {
        if (this.state.typingTimeout) {
            clearTimeout(this.state.typingTimeout);
        }
        this.setState({
            currentPage: 0
        }, () => {
            this.setState({
                query: data,
                typingTimeout: setTimeout(() => {
                    this.setState({
                        loading: true,
                    })
                    this.APIGetRolesList(this.state.currentPage, this.state.limit, this.state.query);
                }, 3000)
            })
        })
    }

    onSearchTableRoles = () => {

        if (this.state.typingTimeout) {
            clearTimeout(this.state.typingTimeout)
        }

        this.setState({
            loading: true,
            currentPage: 0
        }, () => {
            this.APIGetRolesList(0, this.state.limit, this.state.query);
        })
        

    }

    handleOnFilterChange = (filter) => {

        let _filter: any = {};

        if (this.state.typingTimeout) {
            clearTimeout(this.state.typingTimeout)
        }

        filter.map(w =>

            w.columnName === 'status' ?
                w.value !== 'all' ?
                    _filter[w.columnName] = `${w.value}` : ''
                : _filter[w.columnName] = {
                    like: `${w.value}.*`,
                    options: 'i'
                }
        )

        this.setState({
            default_filter: _filter,
            currentPage: 0,
            typingTimeout: setTimeout(() => {
                this.setState({
                    loading: true,
                })
                this.APIGetRolesList(this.state.currentPage, this.state.limit, this.state.query);
            }, 1500)
        })
    }

    handleSortChange = (sort) => {
        this.setState({
            order: `${sort[0].columnName} ${sort[0].direction.toUpperCase()}`,
            loading: true,
        },
            () => {
                this.APIGetRolesList(this.state.currentPage, this.state.limit, this.state.query)
            }
        )
    }

    handleRowClick = (row) => {
        var data = this.state.rows;
        this.setState({
            redirect_to_role: true,
            redirect_data: data[row]
        })
    }

    handleAddRole = () => {
        console.log('handleAddRole');
        this.setState({
            isModuleModalOpen: true,
        })
    }

    handleContinueRole = () => {
        var data = this.state.moduleItems[Number(this.state.moduleValue)].name;
        console.log(data, 'datawew');
        this.setState({
            redirect_to_addrole: true,
            redirect_data: data
        })
    }

    render(): JSX.Element {

        if (this.state.redirect_to_role) {
            return (
                <>
                    <Redirect
                        to={{
                            pathname: '/system-admin/roles/' + this.state.redirect_data._id,
                        }}
                    />
                </>
            );
        }
        if (this.state.redirect_to_addrole) {
            return (
                <Redirect
                    to={{
                        pathname: '/system-admin/add-roles',
                        state: { from: this.state.redirect_data },
                    }}
                />
            );
        }

        return (

            <div>

                {this.state.loading ? <Loader /> : null}
                <Grid container alignItems={'center'} style={{ textAlign: 'center' }} spacing={4}>
                    <Grid item xs={8} md={9}>
                        <ReactSearch
                            placeholder={'Search a role'}
                            onSearch={this.onSearchTableRoles}
                            onChange={this.handleTimeOutSearchTableRoles}
                        />
                    </Grid>
                    <Grid item xs={4} md={3}>
                        <Grid container>
                            <Grid item xs={6}>
                                <FontAwesomeIcon icon={faDownload} className={this.props.classes.addFieldIcon} />
                                <Link className={this.props.classes.linkStyleSelected}>Generate Report</Link>
                            </Grid>
                            <Grid item xs={6}>
                                <FontAwesomeIcon icon={faPlus} className={this.props.classes.addFieldIcon} />
                                <Link className={this.props.classes.linkStyleSelected} onClick={() => this.handleAddRole()}>Add Role</Link>
                            </Grid>
                        </Grid>
                    </Grid>
                </Grid>
                <ReactTable
                    rows={this.state.rows}
                    columns={this.state.columns}
                    message={'There are no user roles to show yet.'}
                    viewOnly={false}
                    enableSelection={false}
                    onRowClick={this.handleRowClick}
                    totalCount={this.state.totalCount}
                    currentPage={this.state.currentPage}
                    pageSize={this.state.limit}
                    defaultFilters={this.state.default_filter}
                    onFilterChange={this.handleOnFilterChange}
                    onSortingChange={this.handleSortChange}
                    onSetPageSize={(size: any) => {
                        this.APIGetRolesList(this.state.currentPage, size, this.state.query);
                    }}
                    onPageChange={(page: any) => {
                        this.APIGetRolesList(page, this.state.limit, this.state.query);
                    }}
                />

                <ModalComponent
                    isModalOpen={this.state.isModuleModalOpen}
                    onClose={() => {
                        this.setState({
                            isModuleModalOpen: false
                        })
                    }}
                    message={'Select a module for the role you will create:'}
                    title={'Choose a Module'}
                    buttons={
                        <Grid container justify="center" alignItems="center" style={{ textAlign: 'center' }}>
                            <Grid item xs={6}>
                                <Button
                                    onClick={() => {
                                        this.setState({
                                            isModuleModalOpen: false
                                        })
                                    }}
                                    variant={'outlined'}
                                    color={'secondary'}
                                    className={this.props.classes.button}
                                >Cancel</Button>
                            </Grid>
                            <Grid item xs={6}>
                                <Button className={this.props.classes.button} variant={'contained'} color={'primary'} onClick={() => this.handleContinueRole()}>Continue</Button>
                            </Grid>
                        </Grid>
                    }
                >
                    <div style={{ width: '90%', margin: 'auto' }}>
                        <BasicSelect
                            menuItems={this.state.moduleItems}
                            width={'100%'}
                            value={this.state.moduleValue}
                            onChange={(e: any) => {
                                this.setState({
                                    moduleValue: e.target.value
                                })
                            }}
                        />
                    </div>
                </ModalComponent>
            </div>
        )
    }
}