import React from 'react'
import { TableComponent } from './TableComponent'

import { userTableColumn, tableConst } from './Accounts.const'
import { getUserList } from 'Pages/API/Calls/User'
import { UI } from 'Components'
import {
    Grid,
} from '@material-ui/core';
import { ReactSearch } from 'Components/UI/SearchBar/ReactSearch';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlus } from '@fortawesome/free-solid-svg-icons';
import Link from '@material-ui/core/Link';
import { Link as RouteTo } from 'react-router-dom'
import { makeStyles } from '@material-ui/core/styles';
import { Redirect } from 'react-router-dom'
import moment from 'moment';

const useStyles = makeStyles({
    addFieldIcon: {
        color: '#3AB77D',
        cursor: 'pointer',
        fontSize: '13px',
        marginRight: '8px',
        marginLeft: '20px'
    },
    searchSection: {
        marginBottom: '21px'
    },
    label: {
        color: '#272E4C',
        fontSize: 13
    },
    value: {
        color: '#3AB77D',
        fontSize: 13,
        fontWeight: 800,
        marginRight: '10px'
    },
    tableTop: {
        padding: '20px'
    },
    searchTabAction: {
        display: 'flex',
        justifyContent: 'flex-end'
    }
})

export const AccountTabPage: React.FC = (): JSX.Element => {
    const classes = useStyles();
    React.useEffect(() => {
        getUsers({ pageSize: 10, offset: 0 }, true)
    }, [])

    // Fetch
    const getUsers = (newState?: any, saveSummary?: boolean) => {
        setLoading(true)
        getUserList(
            newState.pageSize,
            newState.offset,
            newState.where,
            newState.order,
            newState.query).then((data: any) => {
                console.log(data)
                if (!data.error) {
                    const state = {
                        ...newState,
                        rows: data.body,
                        totalCount: data.count
                    }

                    if (saveSummary && saveSummary === true) {
                        setSummary(data.summary)
                        setStatuses(data.statuses)
                    }

                    updateTableState(state)

                } else {
                    const state = {
                        ...newState,
                        rows: [],
                        totalCount: 0
                    }

                    updateTableState(state)
                }

                setLoading(false)
            }).catch(() => {
                console.log("catch")
                setLoading(false)
                const state = {
                    ...newState,
                    rows: [],
                    totalCount: 0
                }

                updateTableState(state)
            })
    }

    // Fetch
    // const getSearchUser = (newState?: any) => {
    //     setLoading(true)
    //     searchUser(
    //         newState.pageSize,
    //         newState.offset,
    //         newState.query,
    //         newState.where,
    //         newState.order).then((data: any) => {

    //             if (!data.error) {
    //                 const state = {
    //                     ...newState,
    //                     rows: data.body,
    //                     totalCount: data.count
    //                 }

    //                 updateTableState(state)
    //             } else {
    //                 const state = {
    //                     ...newState,
    //                     rows: [],
    //                     totalCount: 0
    //                 }

    //                 updateTableState(state)
    //             }

    //             setLoading(false)
    //         }).catch(() => {
    //             setLoading(false)
    //         })
    // }

    // Table event handlers
    const onCurrentPageChange = (value) => {
        const state = {
            pageSize: tableStates.pageSize,
            currentPage: value,
            offset: tableStates.pageSize * value,
            order: tableStates.order,
            where: tableStates.where,
            query: tableStates.query
        }

        getUsers(state)
    }

    const onPageSizeChange = (value) => {
        const state = {
            pageSize: value,
            offset: tableStates.offset,
            order: tableStates.order,
            where: tableStates.where,
            query: tableStates.query
        }

        if (tableStates.currentPage > (Math.ceil(tableStates.totalCount / value) - 1)) {
            state['currentPage'] = ((Math.ceil(tableStates.totalCount / value) - 1))
            state['offset'] = ((Math.ceil(tableStates.totalCount / value) - 1)) * value 
        }

        getUsers(state)
    }

    const handleTimeOutSearch = (value) => {
        if (typingTimeout) {
            clearTimeout(typingTimeout);
        }

        setTypingTimeout(setTimeout(function () {
            const state = {
                currentPage: 0,
                pageSize: tableStates.pageSize,
                offset: 0,
                order: tableStates.order,
                where: tableStates.where,
                query: value
            }

            getUsers(state)

        }, 3000));
    }

    const onFilterChange = (value) => {
        if (typingTimeout) {
            clearTimeout(typingTimeout);
        }

        setTypingTimeout(setTimeout(function () {
            const state = {
                currentPage: 0,
                pageSize: tableStates.pageSize,
                offset: 0,
                order: tableStates.order,
                where: {},
                query: tableStates.query
            }

            value.map(w =>
                w.columnName === 'first_name' ?
                    state.where = {
                        ...state.where,
                        or: [
                            {
                                first_name: {
                                    like: `${w.value}.*`,
                                    options: 'i'
                                }
                            }, {
                                middle_name: {
                                    like: `${w.value}.*`,
                                    options: 'i'
                                }
                            }, {
                                last_name: {
                                    like: `${w.value}.*`,
                                    options: 'i'
                                }
                            }
                        ]
                    }
                : w.columnName === 'group' ?
                    state.where['group_name'] = {
                        like: `${w.value}.*`,
                        options: 'i'
                    }
                : w.columnName === 'roles' ?
                    state.where["main_role.name"] = {
                        like: `${w.value}.*`,
                        options: 'i'
                    }
                : w.columnName === 'status' ?
                    state.where["status"] = w.value
                : state.where[w.columnName] = {
                    like: `${w.value}.*`,
                    options: 'i'
                }
            )

            console.log(state)

            getUsers(state)
        }, 3000));
    }

    const onSortingChange = (value) => {

        let filter = {
            ...value[0],
            direction: value[0].direction.toUpperCase()
        }

        if (filter.columnName === 'roles') {
            filter = {
                ...value[0],
                columnName: 'main_role.name'
            }
        }

        let a = Object.values(filter).join(' ')

        const state = {
            pageSize: tableStates.pageSize,
            offset: tableStates.offset,
            order: [a],
            where: tableStates.where,
            query: tableStates.query
        }

        getUsers(state)
    }

    const onRowClick = (value) => {
        setUserIdSelected(value)
        setIsRowClicked(true)
    }

    // const handleSearchChange = ({target}) => {
    //     setSearchValue(target.value)
    // }

    const updateTableState = (prop) => {
        setTableState({
            ...tableStates,
            ...prop
        })
    }

    const rowDataFixer = (array: any) => {
        let newEntry: any = [];
        if (array.length > 0) {
            array.forEach(data => {
                let newRow = {
                    ...data,
                    first_name: `${data.full_name}`,
                    group: data.hasOwnProperty('group') ? data.group.name : 'N/A',
                    roles: data.hasOwnProperty('roles') && data.roles.length > 0 ? data.roles.map((x, i) => `${x.name}${i !== (data.roles.length - 1) ? ', ' : ''}`) : 'N/A',
                    expiry_date: data.hasOwnProperty('expiry_date') ? moment(data.expiry_date).format('MMM DD, YYYY') : 'N/A',
                    last_login: data.hasOwnProperty('last_login') ? moment(data.last_login).format('MMM DD, YYYY') : 'N/A',
                    status: data.hasOwnProperty('status') ? <span style={{ textTransform: 'capitalize' }}>{data.status}</span> : 'N/A',
                }

                newEntry.push(newRow)
            })

            return newEntry
        }

        return []
    }

    const handleSearchUser = (e) => {
        if (typingTimeout) {
            clearTimeout(typingTimeout);
        }
        const state = {
            currentPage: 0,
            pageSize: tableStates.pageSize,
            offset: 0,
            order: tableStates.order,
            where: tableStates.where,
            query: e
        }

        getUsers(state)

    }

    // States
    const [tableStates, setTableState] = React.useState<any>({
        rows: [],
        currentPage: 0,
        totalCount: 0,
        pageSize: 10,
        offset: 0,
        order: '',
        where: {},
        query: ''
    })

    // const [searchValue, setSearchValue] = React.useState<string>('')
    const [isLoading, setLoading] = React.useState<boolean>(true)
    const [isRowClicked, setIsRowClicked] = React.useState<boolean>(false)
    const [userIdSelected, setUserIdSelected] = React.useState<string>('')
    const [typingTimeout, setTypingTimeout] = React.useState<any>(0)
    // const [query, setQuery] = React.useState<string>('')
    const [summary, setSummary] = React.useState<any>()
    const [statuses, setStatuses] = React.useState<any>()

    if (isRowClicked === true) {
        return <Redirect to={`account/${userIdSelected}`} />
    }

    return (
        <>
            {isLoading && <UI.LoadingIndicator />}
            <Grid
                container xs={12}
                spacing={2}
                direction="row"
                justify="center"
                alignItems="center"
                className={classes.searchSection}>
                <Grid item xs={9}>
                    {/* <OutlinedInput
                        fullWidth={true}
                        id="outlined-adornment-amount"
                        value={query}
                        onChange={handleSearchUser}
                        placeholder="Search user"
                        startAdornment={
                        <InputAdornment position="start">
                            <SearchIcon style={{color: '#363636', opacity: 0.4}} />
                        </InputAdornment>}
                        labelWidth={0} /> */}
                    <ReactSearch
                        onChange={handleTimeOutSearch}
                        placeholder={'Search user'}
                        onSearch={handleSearchUser} />
                </Grid>
                <Grid
                    xs={3}
                    md={3}>
                    <div className={classes.searchTabAction}>
                        <UI.GenerateReport></UI.GenerateReport>
                        <FontAwesomeIcon icon={faPlus} className={classes.addFieldIcon} />
                        <RouteTo to='/system-admin/adduser'>
                            <Link>Add User</Link>
                        </RouteTo>
                    </div>
                </Grid>
            </Grid>
            <div style={{ boxShadow: '0px 3px 6px #00000029' }}>
                <Grid container className={classes.tableTop}>
                    {
                        summary && Object.entries(summary).length > 0 ?
                            <>
                                <Grid
                                    style={{ textAlign: 'center' }}
                                    item xs={4}>
                                    <span className={classes.value}>{summary.total_users}</span>
                                    <span className={classes.label}>TOTAL</span>
                                </Grid>
                                {/* <Grid
                                    style={{ textAlign: 'center' }}
                                    item xs={3}>
                                    <span className={classes.value}>{summary.onlineUsers}</span>
                                    <span className={classes.label}>ONLINE NOW</span>
                                </Grid> */}
                                <Grid
                                    style={{ textAlign: 'center' }}
                                    item xs={4}>
                                    <span className={classes.value}>{summary.active_users}</span>
                                    <span className={classes.label}>ACTIVE</span>
                                </Grid>
                                <Grid
                                    style={{ textAlign: 'center' }}
                                    item xs={4}>
                                    <span className={classes.value}>{summary.deactivated_users}</span>
                                    <span className={classes.label}>INACTIVE</span>
                                </Grid>
                            </>
                            : 'Loading...'
                    }
                </Grid>
                <TableComponent
                    rows={rowDataFixer(tableStates.rows)}
                    columns={userTableColumn}
                    pageSize={tableStates.pageSize}
                    pageSizes={tableConst}
                    currentPage={tableStates.currentPage}
                    totalCount={tableStates.totalCount}
                    statusOptions={statuses}
                    // Events
                    onRowClick={onRowClick}
                    onFilterChange={onFilterChange}
                    onCurrentPageChange={onCurrentPageChange}
                    onPageSizeChange={onPageSizeChange}
                    onSortingChange={onSortingChange} />
            </div>
        </>
    )
} 